import axios from "axios";
import Check from "../utils/check"

/*
All these are Gateway to the server all data leaves the Client and reaches the server
through these methods. 

*/


export const registerUser = (data, history, clbk) => {
    console.log(data)
    axios.post("/auth/register", data).then(result => {
        console.log(result.data);
        var res = result.data
        if (!res.success) clbk(res); else {
            res = JSON.stringify(res)
            localStorage.setItem("token", res);
            Check();
            history.push("/");
        }
    })
}


export const Login1 = (data, history, clbk) => {
    axios.post("/auth/login", data).then(result => {
        console.log(result)
        var res = result.data;
        if (!res.success) clbk(res); else {
            res = JSON.stringify(res)
            localStorage.setItem("token", res);
            Check();
            history.push("/main");
        }
    })
}

export const mailSend = (data, clbk) => {
    var m = {
        email: data
    }
    axios.post("/auth/sendMail", m).then(result => {
        //console.log(result)
        if (result.data.success) clbk(true); else clbk(false);
    })
}