import React, { useState } from "react"
import { useHistory, Link } from "react-router-dom";
import { Login1 } from "../apicall/api";


/*
This page first checks wether we already logen in or not 
if loged in it takes you to secret page.
If not logged in then it takes the email and passwords and sends it to the Server
if the server validation is successfull then it will return a JWT token which 
we store in localStorage.

*/


function Login() {
    const history = useHistory();

    if (localStorage.isAuth) {
        history.push("/main");
    }
    // console.log(props.auth)
    const [login, setLogin] = useState({
        email: "",
        password: "",
    })
    const [err, setErr] = useState({
        email: "",
        password: ""
    })

    function change(e) {
        setErr({})
        const { id, value } = e.target;
        setLogin(prev => ({
            ...prev,
            [id]: value
        }))
    }
    function onSub(e) {
        e.preventDefault();
        console.log(login)
        Login1(login, history, (data) => {
            setErr(data);
        })
    }
    return (
        <div>
            <div className="container flex" style={{ height: "100vh", alignItems: "center", display: "flex" }}>
                <div className="col s12" >
                    <Link to="/" className="btn-flat waves-effect">
                        <i className="material-icons left"></i> Back to
          home
</Link>
                    <div className="col s12" style={{ width: "900px" }}>
                        <h4><b>Login</b> Below</h4>
                    </div>
                    <p className="grey-text text-darken-1">
                        Don't have an account? <Link to="/register">Register</Link>
                    </p>
                    <form noValidate onSubmit={onSub}>

                        <div className="input-field col s12">
                            <input id="email" onChange={change} type="email" />
                            <label htmlFor="email">Email</label>
                            <span className="red-text">
                                {err.email}
                            </span>
                        </div>
                        <br />
                        <div className="input-field col s12">
                            <input type="password" id="password" onChange={change} error={err.password} value={login.password} />
                            <label htmlFor="password">Password</label>
                            <span className="red-text">
                                {err.password}
                            </span>
                        </div>

                        <div className="col s12" style={{ paddingLeft: "11.250px" }}>
                            <button
                                style={{
                                    width: "150px",
                                    borderRadius: "3px",
                                    letterSpacing: "1.5px",
                                    marginTop: "1rem"
                                }}
                                type="submit"
                                className="btn btn-large waves-effect waves-light hoverable orange accent-3"
                            >
                                Login
            </button>
                        </div>

                    </form>
                </div>
            </div>
        </div>
    )
}

export default Login;