import React, { useState } from "react";
import { useHistory, Link } from "react-router-dom";
import { mailSend } from "../apicall/api";

function Main() {
    const history = useHistory();
    if (!localStorage.isAuth) history.push("/");
    function logout() {
        localStorage.removeItem("token");
        localStorage.removeItem("isAuth");
        localStorage.removeItem("email")
        history.push("/")
    }
    const [ans, setAns] = useState({
        success: "",
        err: ""
    })
    const [load, setLoad] = useState(false);
    function mail() {
        setLoad(true);
        var email = localStorage.email;

        // console.log(email);
        mailSend(email, result => {
            if (result) setAns(prev => ({
                ...prev,
                success: "Mail Sent!"
            })); else setAns(prev => ({
                ...prev,
                err: "Network Err!"
            }))
            setLoad(false);
        })
    }
    return (
        <div>
            <div className="container" style={{ height: "100vh", display: "flex", justifyContent: "center", alignItems: "center" }}>
                <div>
                    <Link to="/" className="btn btn-large waves-effect waves-light hoverable blue">
                        <i className="material-icons left"></i> Back to home</Link><br /><br />
                    <button className="btn btn-large waves-effect waves-light hoverable red" onClick={logout}>Logout</button><br /><br />
                    <button className="btn btn-large waves-effect waves-light hoverable green" onClick={mail}>Forgot Password</button>
                    <br />
                    {load ? <span>Sending....</span> : null}
                    {ans.success ? <small style={{ color: "green" }}>{ans.success}</small> : null}
                    {ans.err ? <small style={{ color: "red" }}>{ans.err}</small> : null}
                </div>
            </div>
        </div>
    )
}

export default Main;