import React from "react";
import { Link } from "react-router-dom";

function Landing() {
    return (
        <div style={{ height: "75vh", alignItems: "center", display: "flex" }}>
            <div className="container valign-wrapper flex">
                <div className="row">
                    <div className="flex" >
                        <h1>Basic Login Authentication</h1>
                    </div><br />

                    <div className="col s6 center">
                        <Link to="/register" style={{ height: "5vh", width: "150px" }} className="btn btn-lg waves-light hoverable blue">
                            Register
                    </Link>
                    </div>
                    <div className="col s6 center" >
                        <Link to="/login" style={{ height: "5vh", width: "150px" }} className="btn btn-lg hoverable waves-light green">Login
                        </Link>
                    </div>

                </div>
            </div>
        </div>
    )
}


export default Landing;