import React, { useState } from "react"
import { useHistory, Link } from "react-router-dom";
import { registerUser } from "../apicall/api";


function Register() {
    var history = useHistory();

    if (localStorage.isAuth) {
        history.push("/main");
    }

    const [reg, setReg] = useState({
        name: "",
        email: "",
        password: "",
        Cpassword: ""
    })
    const [err, setErr] = useState({

    })
    function change(e) {
        setErr({})
        const { id, value } = e.target;
        setReg(prev => ({
            ...prev,
            [id]: value
        }))
    }
    function onSub(e) {
        e.preventDefault();
        console.log(reg)
        registerUser(reg, history, (data) => {
            if (data) {
                setErr(data)
                console.log(err)
            }
        })
    }

    return (
        <div>
            <div className="container flex" style={{ height: "100vh", alignItems: "center", display: "flex" }}>
                <div className="col s12" >
                    <Link to="/" className="btn-flat waves-effect">
                        <i className="material-icons left"></i> Back to
      home
</Link>
                    <div className="col s12" style={{ width: "900px" }}>
                        <h4><b>Register</b> Below</h4>
                    </div>
                    <p className="grey-text text-darken-1">
                        Already have an account? <Link to="/login">Login</Link>
                    </p>
                    <form noValidate onSubmit={onSub}>
                        <div className="input-field col s12">
                            <input id="name" onChange={change} type="text" value={reg.name} error={err.name} />
                            <label htmlFor="name">Name</label>
                            <span className="red-text">
                                {err.name}
                            </span>
                        </div>
                        <div className="input-field col s12">
                            <input id="email" onChange={change} type="email" error={err.email} value={reg.email} />
                            <label htmlFor="email">Email</label>
                            <span className="red-text">
                                {err.email}
                            </span>
                        </div>

                        <div className="input-field col s12">
                            <input type="password" id="password" onChange={change} error={err.password} value={reg.password} />
                            <label htmlFor="password">Password</label>
                            <span className="red-text">
                                {err.password}
                            </span>
                        </div>
                        <div className="input-field col s12">
                            <input type="password" id="Cpassword" onChange={change} error={err.Cpassword} value={reg.Cpassword} />
                            <label htmlFor="Cpassword">Confirm Password</label>
                            <span className="red-text">
                                {err.password}
                            </span>
                        </div>
                        <div className="col s12" style={{ paddingLeft: "11.250px" }}>
                            <button
                                style={{
                                    width: "150px",
                                    borderRadius: "3px",
                                    letterSpacing: "1.5px",
                                    marginTop: "1rem"
                                }}
                                type="submit"
                                className="btn btn-large waves-effect waves-light hoverable blue accent-3"
                            >
                                Register
        </button>
                        </div>

                    </form>
                </div>
            </div>
        </div>
    )
}


export default Register;